import { spawn } from 'child_process'
import { createWriteStream as stream } from 'fs'
import { pick, omit, defaultsDeep as defaults } from 'lodash'
import yargs from 'yargs'

export function shell(command, options = {}, handle){
  if (typeof options === 'function') {
     handle = options
     options = {}
  }

  let [cmd, ...args] = command.split(' ')

  let proc = spawn(cmd, args, options)

  proc.stdout.on('data', (data) => handle(data.toString()))
  proc.stderr.on('data', (data) => handle(data.toString()))
  proc.on('error', (data) => handle(data.toString()))

  return proc
}

export function colorize(object, options = {}) {
  let engine = require('./util/colorize')

  engine.setOptions(
    defaults(options, {
      colors:{
        num   : 'cyan',
        str   : 'magenta',
        bool  : 'red',
        regex : 'blue',
        undef : 'grey',
        null  : 'grey',
        attr  : 'green',
        quot  : 'yellow',
        punc  : 'yellow',
        brack : 'yellow',
        func  : 'grey'
      },
      display:{
        func: false,
        date: false,
        xarr: true
      },
      level: {
        show   : false,
        char   : '.',
        color  : 'red',
        spaces : 2,
        start  : 0
      }
    })
  )

  return engine.gen(object, options.level.start)
}



